import React from "react";

const WelcomeVideo = () => {
  return (
    <div className="welcome-video">
      <video
        src="https://webredox.net/demo/wp/patara/videos/restaurant.mp4"
        autoPlay
        loop
        muted
        playsInline
      ></video>
      <div className="welcome-caption">
        <h1>
            Welcome to Patara <br />
            Restaurant
        </h1>
      </div>
    </div>
  );
};

export default WelcomeVideo;
